import cn from 'classnames'
import s from './weather.module.css'
import {
    ArrowDown, ArrowDownLeft,
    ArrowDownRight,
    ArrowLeft,
    ArrowRight,
    ArrowUp,
    ArrowUpLeft,
    ArrowUpRight
} from 'react-bootstrap-icons'
import {FC} from 'react'
import {Main, Wind} from '../redux/types'


interface Props {
    visibility: number,
    wind: Wind,
    mainTemp: Main
}


const About: FC<Props> = (props) => {
    return (
        <div className={cn(s.gridItem, s.gridItemAbout)}>
            <div className={cn(s.windHeader)}>
                
            </div>
            <div className={cn(s.mainFont, s.windTxt)}>
                {props.wind.speed} /c
            </div>
            <div className={cn(s.mainFont, s.windTxt)}>
                 :  <span> </span>
                { props.wind.deg >= 335 || props.wind.deg <= 25 ? <span><ArrowLeft/> </span> : null}
                { 26 <= props.wind.deg && props.wind.deg <= 70 ? <span><ArrowUpLeft/> -</span>  : null}
                { 71 <= props.wind.deg && props.wind.deg <= 115 ? <span><ArrowUp/> </span>  : null}
                { 116 <= props.wind.deg && props.wind.deg <= 160 ? <span><ArrowUpRight/> -</span>  : null}
                { 161 <= props.wind.deg && props.wind.deg <= 205 ? <span><ArrowRight/> </span>  : null}
                { 206 <= props.wind.deg && props.wind.deg <= 250 ? <span><ArrowDownRight/> - </span>  : null}
                { 251 <= props.wind.deg && props.wind.deg <= 295 ? <span><ArrowDown/> </span>  : null}
                { 296 <= props.wind.deg && props.wind.deg <= 339 ? <span><ArrowDownLeft /> -</span>  : null}
            </div>
            <div className={cn(s.mainFont, s.windTxt)}>
                <div>
                    : {props.visibility /1000}
                </div>
                <div>
                    : {props.mainTemp.humidity} %
                </div>
            </div>
        </div>
    )
}

export default About